import React, {Component} from "react";
import "./Footer.scss"
import {APP_NAME} from "../../constants";
import * as translator from "../../translate";

class Footer extends Component {

    CACHE_LANG_KEY = "SELECTED_LANGUAGE";
    state = {
        language: "en",
        availableLanguages: [
            {code: "en", "en-title": "English", "zh-title":"英语", "zh-traditional-title":"英語"},
            {code: "zh", "en-title": "Chinese - Simple", "zh-title":"", "zh-traditional-title":""},
            {code: "zh-traditional", "en-title": "Chinese - Traditional", "zh-title":"", "zh-traditional-title":""},
        ],
        languageCodes: {
            "en": "en",
            "en-US": "en",
            "zh": "zh",
            "zh-hk": "zh",
            "zh-sg": "zh",
            "zh-tw": "zh",
            "zh-traditional": "zh-traditional"
        }

    };

    constructor(props) {
        super(props);
        translator.setElementKey("ling-translate");
    }

    componentDidMount() {
        let browserLanguage = window.localStorage.getItem(this.CACHE_LANG_KEY) || window.navigator.language;
        this.setGlobalLanguage(this.state.languageCodes[browserLanguage]);
    }

    setLanguage = (event) => {
        let code = event.target.value;
        this.setGlobalLanguage(code);

    };

    setGlobalLanguage = (code) => {
        if (!code) return;
        window.localStorage.setItem(this.CACHE_LANG_KEY, code);
        this.setState({language: code});
        translator.translateContent(code);
    };

    render() {

        const {availableLanguages, language} = this.state;

        return (
            <div id="FooterComponent">

                <h2 className="color-white">{APP_NAME}</h2>

                <h4 id="languageSelectLabel">Language</h4>

                <select value={language} id="languageSelect" onChange={this.setLanguage}>
                    {availableLanguages.map((lang, key) => {
                        return <option key={key} value={lang.code}>{lang.title}</option>
                    })}
                </select>

            </div>
        )
    }
}

export default Footer;